import "./styles.css";
import menu from "./menu";

function menus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);
  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

export default function App() {
  return (
    <>
      <h1>メニュー</h1>
      {menus(0)}
      {menus(1)}
      {menus(2)}
      {menus(3)}
    </>
  );
}

